.no-support {
  font-size: 0.32rem;
  margin-top: 1rem;
}

.social-list-top {
  margin-top: 0.28rem;
  border-top: 1px solid rgba(0,0,0,0.12);
}

.social-list {
  text-align: left;
  height: 0.88rem;
  line-height: 0.88rem;
  background: #FFF;
  font-size: 0;
  border-bottom: 1px solid rgba(0,0,0,0.12);
}

.social-list-l {
  width: 2.5rem;
  font-size: 0.32rem;
  text-indent: 0.32rem;
  text-align: left;
  display: inline-block;
}

.social-list-r {
  width: 4.5rem;
  font-size: 0.32rem;
}

.social-list-title {
  width: 40%;
  margin: 0 5%;
  font-size: 0.3rem;
  text-align: center;
  display: inline-block;
}

.social-list-title.active {
  color: #B60005;
  border-bottom: 1px solid #B60005;
}

.common-btn {
  background: #9B9B9B;
  height: 0.88rem;
  line-height: 0.88rem;
  text-align: center;
  font-size: 0.32rem;
  border-radius: 0.08rem;
  color: #fff;
}

.common-btn-active {
  background: #B60005;
}

.social-btn {
  margin: 0.44rem 0.32rem 0;
}


