[v-cloak] {
  display: none;
}

html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: 'Hiragino Sans GB W3','Hiragino Sans GB','Microsoft YaHei','WenQuanYi Micro Hei',sans-serif;
  position: relative;
}

body {
  background: #F0F0F0;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width: 100%;
  height: 100%;
  color: #2c3e50;
}

.scroll-box {
  width: 100%;
  position: absolute;
  top: 0.88rem;
  bottom: 0.96rem;
  overflow-y: scroll;
  overflow-x: visible;
  -webkit-overflow-scrolling: touch;
}

.scroll-box-top {
  top: 0;
}

#view {
  width: 100%;
  height: 100%;
}

.scroll-box-bottom {
  bottom: 0;
}

.view {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.fade-enter {
  opacity: 1;
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}

.fade-leave {
  opacity: 0;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.back-enter {
  opacity: 1;
  -webkit-transform: translate3d(-110%, 0, 0);
  transform: translate3d(-110%, 0, 0);
}

.back-leave {
  opacity: 0;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.v-link-active {
  color: red;
}

.custom-active-class {
  color: #f60;
}


