.reg-tit {
  height: 0.88rem;
  line-height: 0.88rem;
  background: #FAFAFA;
  border-bottom: 0.01rem solid rgba(0,0,0,0.12);
  font-size: 0.32rem;
  color: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}

.reg-tit p {
  width: 100%;
}

.reg-tit a {
  width: 10%;
  height: 0.7rem;
  position: absolute;
  left: 0;
  top: 0.18rem;
  padding-left: 0.5rem;
}

.reg-tit a:before {
  position: absolute;
  left: 0.1rem;
  top: 0.08rem;
  width: 0;
  height: 0;
  content: '';
  border: 0.2rem solid transparent;
  border-right: 0.2rem solid #333333;
}

.reg-tit a:after {
  position: absolute;
  left: 0.12rem;
  top: 0.08rem;
  width: 0;
  height: 0;
  content: '';
  border: 0.2rem solid transparent;
  border-right: 0.2rem solid #FAFAFA;
}


