.scroll-bg {
  background: #fff;
  top: 1.84rem;
  bottom: 0;
}

.search-city-text {
  position: absolute;
  width: 100%;
  top: 0.88rem;
  padding: 0.12rem 0.24rem;
  text-align: left;
  background: #fff;
}

.search-city-text-active .search-input {
  text-align: left;
  width: 6.2rem;
}

.search-city-text-active .search-cancel {
  display: inline-block;
}

.search-cancel {
  font-size: 0.32rem;
  color: #B60005;
  padding-left: 0.1rem;
  text-align: center;
  display: none;
}

.search-icon {
  width: 0.32rem;
  height: 0.32rem;
  background: #fff url("../assets/images/search-icon.png") no-repeat center center;
  background-size: cover;
  position: absolute;
  top: 0.32rem;
  left: 1.5rem;
}

.search-city-text-active .search-icon {
  left: 0.4rem;
}

.search-input {
  background: #fff;
  height: 0.72rem;
  line-height: 0.72rem;
  padding: 0 0.5rem;
  width: 100%;
  font-size: 0.3rem;
  text-align: center;
  border: 1px solid #d8d8d8;
  border-radius: 0.08rem;
}

.search-city-list {
  background: #fff;
  border-top: 1px solid #d8d8d8;
}

.search-city-title {
  font-size: 0.24rem;
  height: 0.8rem;
  text-align: left;
  text-indent: 0.32rem;
  line-height: 0.8rem;
  border-bottom: 1px solid #d8d8d8;
  color: rgba(0,0,0,0.54);
}

.search-city-item {
  text-align: left;
}

.search-city-item > li {
  font-size: 0.28rem;
  min-height: 0.96rem;
  line-height: 0.96rem;
}

.search-city-item > li span {
  position: relative;
  margin: 0 0.32rem;
  height: 0.95rem;
  line-height: 0.95rem;
  display: block;
  border-bottom: 1px solid #d8d8d8;
}

.search-city-item > li li span:last-child {
  border-bottom: 0;
}

.prompt-txt {
  color: #999;
  display: inline-block;
}

.hot-search-list .search-city-item li:last-child span {
  border-bottom: 0;
}

.more-icon {
  width: 0.16rem;
  height: 0.28rem;
  background: url("../assets/images/more-icon.png") no-repeat right center;
  background-size: cover;
  position: absolute;
  right: 0;
  top: 0.34rem;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.more-top-icon {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

.more-bottom-icon {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.serach-city-sec {
  background: #f4f4f4;
  padding: 0 0.32rem 0 0.64rem;
}

.serach-city-sec li {
  height: 0.96rem;
  line-height: 0.96rem;
  position: relative;
  border-bottom: 1px solid #d8d8d8;
}


